<template>

            <q-page class="flex flex-center">
                <div id="map">
                </div>
                <MapPopup />
            </q-page>
   
</template>
 
<script>
import { Map, View } from 'ol';
import { Tile as TileLayer } from 'ol/layer';
import { OSM } from 'ol/source';
import MapPopup from '@/components/MapPopup.vue'
import "ol/ol.css";

export default {
    name: 'OlMap',
    components: {
        MapPopup
    },
    mounted() {
        this.map = new Map({
            target: 'map',
            layers: [
                new TileLayer({
                    source: new OSM(),
                    name: 'OSM'
                })
            ],
            view: new View({
                center: [0, 0],
                zoom: 2
                // center: [-9869708.964428628, 4908948.052576464],
                // zoom: 4
            })
        })
        // Mutate the map object or change the map state
        this.$store.commit('setMap', this.map);
    }
}
</script>
<style scoped>
#map {
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
}
</style>